/* Reset CSS detail */
.page-detail {
  background-color: #FAFAFA;
  padding: 20px 0;
}

.detail_tin {
  position: relative;
}
.detail_tin .flex-date {
  display: flex;
  align-items: center;
  font-size: 16px;
  margin-bottom: 24px;
}
.detail_tin .flex-date .breadcrumb {
  background: none;
  align-items: center;
  padding: 0;
}
.detail_tin .flex-date .breadcrumb li {
  margin-bottom: 0;
}
.detail_tin .flex-date .breadcrumb a {
  color: #4F4F4F;
}
.detail_tin .flex-date .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  border: 0;
  width: 4px;
  height: 4px;
  background: #4F4F4F;
  top: 11px;
  left: 14px;
  border-radius: 50%;
}
.detail_tin .flex-date .breadcrumb .breadcrumb-item.active {
  color: #4F4F4F;
}
.detail_tin .flex-date .date {
  color: #6A6377;
  font-size: 14px;
  display: flex;
  align-items: center;
  position: relative;
  margin-left: 24px;
}
.detail_tin .flex-date .date svg {
  margin-right: 5px;
}
.detail_tin .flex-date .date::before {
  content: "";
  background: #6A6377;
  width: 1px;
  height: 15px;
  position: absolute;
  left: -12px;
  top: 3px;
}
.detail_tin .title-detail {
  font-size: 24px;
  font-weight: bold;
  margin: 0 0 15px 0;
  position: relative;
  color: #1C1C28;
}
.detail_tin .title_detail {
  font-size: 32px;
  font-weight: 600;
  margin: 0 0 24px 0;
  position: relative;
}
.detail_tin p.lead {
  font-weight: bold;
  font-size: 16px;
}
.detail_tin p.lead .slogan {
  background: #303030;
  color: #fff;
  padding: 2px 5px;
}
.detail_tin ul {
  padding-left: 20px;
}
.detail_tin ul li {
  margin-bottom: 5px;
  list-style-type: disc;
}
.detail_tin ol {
  padding-left: 20px;
}
.detail_tin ol li {
  margin-bottom: 5px;
  list-style-type: inherit;
}
.detail_tin .embed_video {
  text-align: center;
  margin-bottom: 15px;
}
.detail_tin .embed_video iframe {
  max-width: 100%;
}
.detail_tin h2,
.detail_tin h3 {
  font-size: 25px;
  font-weight: 700;
  margin: 0 0 10px 0;
  line-height: 1.2;
}
.detail_tin p {
  line-height: 1.6;
  margin-bottom: 15px;
}
.detail_tin p a {
  color: #c36;
}
.detail_tin p img {
  max-width: 100%;
  margin: 0 auto;
  display: block;
}
.detail_tin table {
  margin-block-end: 15px;
}
.detail_tin table th, .detail_tin table td {
  padding: 15px;
  line-height: 1.5;
  vertical-align: top;
  border: 1px solid hsla(0, 0%, 50.2%, 0.5019607843);
}
.detail_tin table tr:nth-child(odd) td {
  background-color: hsla(0, 0%, 50.2%, 0.0705882353);
}
.detail_tin .Images {
  text-align: center;
}
.detail_tin .Images img {
  margin: 0 auto;
  display: block;
}
.detail_tin .Images span {
  display: block;
  margin: 10px 0;
  color: #666;
  font-size: 14px;
}
.detail_tin .days {
  font: 700 25px var(--font1);
  text-transform: uppercase;
}
.detail_tin .social {
  align-items: center;
  color: rgba(0, 0, 0, 0.45);
  font-size: 16px;
  justify-content: end;
  gap: 5px;
}
.detail_tin .social span {
  margin-right: 3px;
}
.detail_tin .social a {
  background: #6A6377;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  color: #fff;
  font-size: 14px;
}
.detail_tin .social a:hover {
  background: var(--colormain);
  border: 1px solid var(--colormain);
  color: #fff;
}
.detail_tin .social_pin {
  position: absolute;
  left: 60px;
  top: 300px;
}
.detail_tin .social_pin a {
  display: block;
  margin-bottom: 20px;
}
.detail_tin .share_face {
  background: #507BCA;
  border-radius: 3px;
  width: 250px;
  height: 55px;
  font-size: 18px;
  line-height: 55px;
  color: #fff !important;
  display: inline-block;
  cursor: pointer;
  position: relative;
}
.detail_tin .share_face svg {
  width: 18px;
  height: 18px;
}
.detail_tin .share_face .icon {
  position: absolute;
  left: 20px;
}
.detail_tin .share_face .icon::before {
  content: "";
  background: rgba(255, 255, 255, 0.2);
  height: 20px;
  width: 1px;
  position: absolute;
  top: 18px;
  right: -12px;
}
.detail_tin__v2 p {
  font-size: 18px;
  color: #1C1C28;
}
.detail_tin .list-small-article .article-itemt .article-thumb {
  width: 200px;
}
.detail_tin .list-small-article .article-itemt .article-content .title_new {
  font-size: 16px;
}
@media (max-width: 1025px) {
  .detail_tin .title_detail {
    font-size: 26px;
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .detail_tin {
    padding: 0;
    background: none !important;
  }
  .detail_tin .flex-date {
    flex-wrap: wrap;
    margin-bottom: 15px;
  }
  .detail_tin .flex-date .breadcrumb {
    width: 100%;
    margin-bottom: 10px;
    flex-wrap: wrap;
    white-space: nowrap;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
  }
  .detail_tin .flex-date .date {
    margin-left: 0;
  }
  .detail_tin .flex-date .date::before {
    display: none;
  }
}